@keyframes pulse {
  50% {
    transform: scale(90%);
  }
}

@keyframes pulseFade {
  0% {
    opacity: .02;
    transform: scale(120%);
  }

  50% {
    opacity: .15;
    transform: scale(80%);
  }

  100% {
    opacity: .02;
    transform: scale(120%);
  }
}

@keyframes growFadeIn {
  0% {
    transform: scale(0.7);
    opacity: 0;
  }

  80% {
    transform: scale(1.1);
    opacity: 1;
  }

  100% {
    transform: scale(1);
  }
}

@keyframes growFadeOut {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.1);
    opacity: 1;
  }

  100% {
    transform: scale(0.7);
    opacity: 0;
  }
}

@keyframes growIn {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

@keyframes growOut {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(0);
  }
}

@keyframes popIn {
  from {
    transform: scale(0.85);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes popOut {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(0.85);
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes blinkFade {
  50% {
    opacity: 0.5;
  }
}

@keyframes blinkGrow {
  50% {
    transform: scale(1.3);
  }
}

@keyframes blinkUpDown {
  50% {
    transform: translate(0, -5px)
  }
}